import React from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import { PostalCodeInput } from "./components/PostalCodeInput";
import { DigitInput } from "./components/DigitInput";
import { DateInput } from "./components/DateInput";
import { TextField } from "./components/TextField";

type FormProps = {
  postalCode: string;
  date: string;
  digit: string;
  text: string;
  name: string;
  fruit: string;
};

const fruits = [{ name: "apple" }, { name: "pine" }, { name: "orange" }];

type SubmitState = {
  postalCode: string;
  date: string;
  digit: string;
  text: string;
  name: string;
  fruit: string;
};

const initialValues = {
  postalCode: "",
  date: "",
  digit: "",
  text: "",
  name: "",
  fruit: "",
};

const submitInitialState: SubmitState = initialValues;

type SubmitAction = { type: "update"; payload: SubmitState };

function submitReducer(state: SubmitState, action: SubmitAction) {
  switch (action.type) {
    case "update":
      return { ...state, ...action.payload };
    default:
      throw new Error();
  }
}

type ForcusState = {
  postalCode?: boolean;
  date?: boolean;
  digit?: boolean;
  text?: boolean;
  name?: boolean;
  fruit?: boolean;
};

const validationSchema = (forcusState: ForcusState) => {
  return yup.object().shape({
    postalCode: yup
      .string()
      .matches(/^[0-9]{3}[0-9]{4}$/)
      .required(),
    date: yup
      .string()
      .matches(/^[0-9]{4}[0-9]{2}[0-9]{2}$/)
      .required(),
    digit: yup
      .string()
      .matches(/^[0-9]{4}[0-9]{4}[0-9]{4}[0-9]{4}$/)
      .required(),
    name: yup.lazy((name) => {
      if (forcusState.name) {
        return yup
          .string()
          .matches(/^[a-zA-Z]*$/)
          .required();
      }
      return yup
        .string()
        .matches(/^[A-Z]*$/)
        .required();
    }),
    fruit: yup.string().required(),
  });
};

function numberString(val: string) {
  const zenkaku = "０１２３４５６７８９".split("");
  const reg = new RegExp("(" + zenkaku.join("|") + ")", "g");
  const ret = val
    .replace(reg, (match) => String(zenkaku.indexOf(match)))
    .replace(/[^0-9]/g, "");
  return ret;
}

function App() {
  const [state, dispatch] = React.useReducer(submitReducer, submitInitialState);
  const focusStateRef = React.useRef<ForcusState>({});

  const formik = useFormik<FormProps>({
    initialValues: submitInitialState,
    validateOnBlur: true,
    validateOnChange: true,
    validationSchema: validationSchema(focusStateRef.current),
    onSubmit: (values) => {
      dispatch({ type: "update", payload: values });
    },
  });

  return (
    <div style={{ margin: 30 }}>
      <div>Hello React</div>
      <form
        onSubmit={(e) => {
          formik.handleSubmit(e);
        }}
        onKeyDown={(e) => {
          if (e.key === "Enter") {
            focusStateRef.current.name = false;
            formik.handleSubmit();
          }
        }}
      >
        <div>
          <PostalCodeInput
            name="postalCode"
            placeholder="postal-code"
            value={formik.values.postalCode}
            onChange={(value: string) => {
              formik.setFieldValue("postalCode", value);
            }}
          />
          <span>{formik.touched.postalCode && formik.errors.postalCode}</span>
        </div>
        <div>
          <DateInput
            name="date"
            placeholder="date"
            value={formik.values.date}
            onChange={(value: string) => {
              formik.setFieldValue("date", value);
            }}
          />
          <span>{formik.touched.date && formik.errors.date}</span>
        </div>
        <div>
          <DigitInput
            name="digit"
            value={formik.values.digit}
            onChange={(value: string) => {
              formik.setFieldValue("digit", value);
            }}
          />
          <span>{formik.touched.digit && formik.errors.digit}</span>
        </div>
        <div>
          <input
            type="text"
            placeholder="name"
            name="name"
            autoComplete="off"
            value={formik.values.name}
            onChange={(e) => {
              formik.handleChange(e);
            }}
            onFocus={() => {
              focusStateRef.current.name = true;
            }}
            onBlur={(e) => {
              focusStateRef.current.name = false;
              formik.setFieldValue("name", e.target.value.toUpperCase());
            }}
          />
          <span>{formik.touched.name && formik.errors.name}</span>
        </div>
        <div>
          <TextField
            name="text"
            placeholder="text"
            value={formik.values.text}
            maxLength={16}
            autoComplete="off"
            onChange={(e) => {
              formik.handleChange(e);
            }}
            onBlur={(e) => {
              formik.handleBlur(e);
              formik.setFieldValue(
                e.target.name,
                numberString(e.target.value),
                true
              );
            }}
          />
          <span>{formik.touched.text && formik.errors.text}</span>
        </div>
        <div>
          <select name="fruit" onChange={formik.handleChange}>
            <option value="" label="Select a favorite Fruit" />
            {fruits.map((fruit) => (
              <option key={fruit.name} value={fruit.name}>
                {fruit.name}
              </option>
            ))}
          </select>
          <span>{formik.touched.fruit && formik.errors.fruit}</span>
        </div>
        <div>
          <input
            type="submit"
            value="submit"
            onClick={() => {
              focusStateRef.current.name = false;
            }}
          />
        </div>
      </form>
      <div style={{ marginTop: 30 }}>
        <div>
          <span>postal code: </span>
          <span>{state.postalCode}</span>
        </div>
        <div>
          <span>date: </span>
          <span>{state.date}</span>
        </div>
        <div>
          <span>digit: </span>
          <span>{state.digit}</span>
        </div>
        <div>
          <span>name: </span>
          <span>{state.name}</span>
        </div>
        <div>
          <span>text: </span>
          <span>{state.text}</span>
        </div>
        <div>
          <span>fruit: </span>
          <span>{state.fruit}</span>
        </div>
      </div>
    </div>
  );
}

export default App;
